<template>
  <div class="card__container">
    <Card 
      v-for="(card,index) in cardListing"
      :key="card.id"
      :index="index"
      :img="card.img"
      :rarity="card.rarity"
      :gold="card.gold"
      :sound="card.sound"
      customClass=""
    />
    
  </div>
  <button ref="button" v-if="packageLeft" @click="getnewPackage()">Nouveau paquet ({{Math.ceil(this.package / 5)}})</button>
</template>

<script>
import anime from 'animejs'
import { mapState} from 'pinia'
import { mapActions} from 'pinia'
import {collectionCardsStore} from "@/stores/collectionCards"
import cardList from "../cardList.json"
import Card from '@/components/Card.vue'
export default {
  name: 'CardContainer',  
  data(){
    return{
      cardCollection : cardList,
      cardListing : [],
      cardUsed: [],
      package : cardList.length,
      packageLeft : true
    }
  },
  components: {
    Card,
  },
  computed:{
        ...mapState(collectionCardsStore,['collectionArr']),
  },
  methods:{
    ...mapActions(collectionCardsStore,['addCardIntoCollection']),

    getRandomCards(arr){
      this.cardListing = []
      for(let i=0 ; i < 5; i++ ){
        let random = this.generateRandomNumber(arr)
        this.cardListing.push(arr[random])
      }
      if(this.cardUsed.length == this.cardCollection.length){
        this.packageLeft = false
      }else{
        this.package -= 5
      }
    },

    generateRandomNumber(arr){
      let usedArray = this.cardUsed
      let random = Math.floor(Math.random() * arr.length)
      if(!usedArray.includes(random)){
          usedArray.push(random)
          this.addCardIntoCollection(random)
          return random
      }
      return this.generateRandomNumber(arr)  
    },
    getnewPackage(){
      const targetButton = this.$refs.button

       anime({
          targets:targetButton,
          scale: .95,
          duration:100,
          complete: function() {
            anime({
              targets:targetButton,
              scale: 1,
              duration:100,
            })
          }
        })
      this.getRandomCards(this.cardCollection)
    }


  },

  mounted(){
    this.getRandomCards(this.cardCollection)
  }

}
</script>
