<template>
    <div class="collection__container" ref="card">
    <button class="collection__button" @click="this.hideCollection">X</button>
    <Card 
      v-for="(card,index) in cardRevealed"
      :key="card.id"
      :index="index"
      :img="card.img"
      :rarity="card.rarity"
      :gold="card.gold"
      :sound="card.sound"
      customClass="revealed big"

    />
    </div>
</template>

<script>
import { mapState} from 'pinia'
import { mapActions} from 'pinia'

import {collectionCardsStore} from "@/stores/collectionCards"
import cardList from "../cardList.json"
import Card from '@/components/Card.vue'

export default {
    name: 'Collection',
    data(){
        return{
            cardListing : cardList,
            cardCollection : this.collectionArr,
            cardRevealed: []
        }
    },
    components: {
        Card,
    },

    computed:{
        ...mapState(collectionCardsStore,['collectionArr' , 'isVisible']),
    },
    methods:{
        ...mapActions(collectionCardsStore,['hideCollection']),

        updateRevelead(arr){
            for(let i=0; i<arr.length; i++){
                this.cardRevealed.push(this.cardListing[arr[i]])
            }
        }
    },
    mounted(){
        console.log(this.cardRevealed)
        this.updateRevelead(this.collectionArr)
    }


}
</script>

