<template>
    <div :class="'card '+rarity+' '+ customClass" ref="card" @click="revealFront()">
        <img ref="cardFront"  class="card-img card-front" :src="require('@/assets/img/cards/' + img + '.png')" alt="">
        <img ref="cardBack" class="card-img card-back" src="@/assets/img/card_back.png" alt="">
        <audio ref="sound"></audio>
    </div>
</template>

<script>

import rare from '@/assets/sound/rare.mp3'
import epic from '@/assets/sound/epic.mp3'
import anime from 'animejs'
import legendaire from '@/assets/sound/legendaire.mp3'



export default {

    name: 'Card',
    props:["img","rarity","gold","sound","customClass"],
    methods:{
        revealFront(){
            let audio = this.getSound();
            if(!this.$refs.card.classList.contains("revealed") && audio != undefined){
                audio.play()
            }
            this.$refs.card.classList.add("revealed")
        },
        getSound(){
            switch(this.sound){
                case 'rare':
                    return new Audio(rare);
                case 'epic':
                    return new Audio(epic);
                case 'legendaire':
                    return new Audio(legendaire);
                default:
                    console.log('oups');
            }
        },
        animationCard(){
            const targetCard = this.$refs.card
            anime({
              targets:targetCard,
              opacity: [0, 1],
              duration: 500,
              easing: 'linear'
            })
        }
    },
    mounted(){
        this.animationCard()
    }
  
}
</script>

