<template>
  <div class="container">
    <div class="container__left">
      <HeroContainer />
      <button ref="button" @click="launchCollection()">Collection</button>

    </div>
    <div class="container__right">
      <CardContainer />
    </div>
    <div id="collection" class="collection">
      <Collection v-if="isVisible" />
    </div>
  </div>
</template>

<script>

import { mapState} from 'pinia'
import { mapActions} from 'pinia'
import {collectionCardsStore} from "@/stores/collectionCards"

import CardContainer from './components/CardContainer.vue'
import HeroContainer from './components/HeroContainer.vue'
import Collection from './components/Collection.vue'

import anime from 'animejs'

export default {
  name: 'App',
  components: {
    CardContainer,
    HeroContainer,
    Collection
  },
  computed:{
    ...mapState(collectionCardsStore,['isVisible']),
  },
  methods:{
    ...mapActions(collectionCardsStore,['showCollection']),


    launchCollection(){
      const targetButton = this.$refs.button
      anime({
        targets:targetButton,
        scale: .95,
        duration:100,
        complete: function() {
          anime({
            targets:targetButton,
            scale: 1,
            duration:100,
          })
        }
      })
      this.showCollection()
    }
  }  
}
</script>

